.cityName {
  color: white;
  font-size: 10vw;
  text-align: center;
}

.cityName a {
  display: block;
  transition: all 0.2s ease;
}

.cityName a:hover {
  transform: scale(1.1);
}

@media screen and (max-width: 900px) {
  .cityName {
    font-size: 15vw;
  }
}

@media screen and (max-width: 600px) {
  .cityName {
    font-size: 15vw;
  }
}
