.result__weatherInfo {
  text-align: center;
  font-size: 25px;
  display: flex;
  justify-content: space-around;
  padding: 30px 0;
  width: 100%;
  box-sizing: border-box;
  position: relative;
  z-index: 1;
  color: rgb(255, 255, 255);
  border-bottom: 2px solid rgba(255, 255, 255, 0.753);
}

.result__weather {
  font-size: 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.result__weather img {
  display: block;
  height: 50px;
  width: 50px;
  background-color: rgba(255, 255, 255, 0.226);
  border-radius: 50%;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.226);
}

.result__humidity {
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 600px) {
  .result__weatherInfo {
    flex-direction: column;
    align-items: center;
    border-top: 2px solid rgba(255, 255, 255, 0.753);
  }

  .result__weather,
  .result__pressure,
  .result__humidity,
  .result__windSpeed {
    display: flex;
    flex-direction: row;

    margin: 5px;
  }
  .result__paragraph {
    margin-right: 15px;
  }
  .result__weather_icon {
    margin-right: 15px;
  }
}
