.result__weatherTimeLapse {
  display: flex;
  justify-content: space-around;
  box-sizing: border-box;
  padding: 30px 0;
}

@media screen and (max-width: 600px) {
  .result__weatherTimeLapse {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    padding: 30px 0;
    row-gap: 30px;
  }
}
